<template>
  <div id="single-blog">
     <h2>{{blog.title}}</h2>
      <article>{{blog.content}}</article>
      <p>作者：{{blog.author}}</p>
      <p>分类：</p>
      <ul>
        <li v-for="(category,index) in blog.categories" :key="index">
            {{category}}
        </li>
      </ul>
      <button @click="deleteSingleBlog()">删除</button>
      <router-link :to="'/edit/' + id">编辑</router-link>
  </div>
</template>

<script>
export default {
  components:{},
  props:{},
  data(){
    return {
        id:this.$route.params.id,
        blog:{}
    }
  },
  created(){
      this.$axios.get("posts/" + this.id + ".json").then((data)=>{
          this.blog = data.data;
      })
  },
  methods:{
    deleteSingleBlog(){
      this.$axios.delete("posts/" + this.id +".json")
      .then(response=>{
        this.$router.push({path:"/"})
      })
    }
  }
}
</script>
<style scoped>
#single-blog{
    max-width: 960px;
    margin:0 auto;
    padding: 20px;
    background: #eee;
    border: 1px dotted #aaa;
}
li{
  height: 30px;
}
button{
  display: inline-block;
  width: 50px;
  height: 30px;
  background-color: crimson;
  border-radius: 5px;
}
.router-link-active{
    background: rgba(255,255,255, .8);
    color: #444;
}
a{
   color:black;
   text-decoration: none;
   border-radius: 5px;
   padding: 12px; 
}
</style>