<template>
  <div class="ShowBlogs" v-theme:column="'narrow'">
      <h1>博客总览</h1>
      <!-- 实现过滤器的功能 -->
      <input type="text" v-model="search" placeholder="搜索">
      <div class="sing-blog" v-for="(blog,index) in filteredBlogs" :key="index">
          <!-- 实现单个页面的跳转 -->
          <router-link :to="'/blog/' + blog.id"><h2 v-rainbow>{{blog.title | to-uppercase}}</h2></router-link>
          <article>{{blog.content | snack}}</article>
      </div>
  </div>
</template>
<script>
export default {
  components:{},
  props:{},
  data(){
    return {
        blogs:[],
        search:""
    }
  },
  created(){
      this.$axios.get("posts.json").then((data)=>{
          console.log(data);
         return data.data;
      }).then((data)=>{
          var blogsArray = [];
         for(let key in data){
             data[key].id = key;
             blogsArray.push(data[key]);
         }
         this.blogs = blogsArray;
      })
  },
  computed:{
      filteredBlogs(){
        return this.blogs.filter((blog)=>{
            return blog.title.match(this.search)
        })
      }
  },
// 组件内局部的自定义指令
directives:{
    "rainbow":{
        bind(el,binding,vnode){
            el.style.color = "#" + Math.random().toString(16).slice(2,8);
        }
    }
},
// 设置局部的过滤器
filters:{
  toUppercase(value){
        return value.toUpperCase();
      },
  "snack":function(value){
     return value.slice(0,100)+ "..."
  }
}
}
</script>
<style scoped>
.ShowBlogs{
    max-width: 800px;
    margin: 0 auto;
}
.sing-blog{
    padding: 20px;
    margin:20px 0;
    box-sizing: border-box;
    background: #eee;
    border:1px dotted #aaa;
}
#show-blogs{
    color: #444;
    text-decoration: none;
}
input[type="text"]{
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
}
</style>