<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form>
        <label>博客标题</label>
        <input type="text" v-model="blog.title">
        <label>博客内容</label>
        <textarea v-model="blog.content"></textarea>
        <br>
        <div id="checkboxes">
              <label>Vue.js</label>
              <input type="checkbox" value="Vue.js" v-model="blog.categories">
              <label>Node.js</label>
              <input type="checkbox" value="Node.js" v-model="blog.categories">
              <label>React.js</label>
              <input type="checkbox" value="React.js" v-model="blog.categories">
              <label>Angular4.js</label>
              <input type="checkbox" value="Angular4.js" v-model="blog.categories">
          </div>
        <label>作者</label>
        <select>
            <option v-for="(author,index) in authors" :key="index">
                {{author}}
            </option>
        </select>
        <button @click.prevent="post">添加博客</button>
    </form>
    <!-- 添加博客成功时显示 -->
    <div v-if="submited"> 
        <h3>您的博客发布成功</h3>
    </div>
    <div id="preview">
          <h3>博客总览</h3>
          <p>博客标题：{{blog.title}}</p>
          <p>博客内容：</p>
          <p>{{blog.content}}</p>
          <p>博客分类</p>
          <ul>
              <li v-for="(category,index) in blog.categories" :key="index">
                  {{category}}
              </li>
          </ul>
          <p>作者：{{blog.author}}</p>
      </div>
  </div>
</template>

<script>
export default {
  components:{},
  props:{},
  data(){
    return {
      blog:{
          title:"",
          content:"",
          categories:[],
      },
      authors:["查鹏1","查鹏2","查鹏3","查鹏4"],
      submited:false
    }
  },
  methods:{
    post:function(){
        // 配置全局的axios
        this.$axios.post("posts.json",this.blog).then((data)=>{
            console.log(data);
            this.submited = true
        })
    }
}
}

</script>
<style scoped>
#add-blog *{
     box-sizing: border-box;
 }
 #add-blog{
     margin: 20px auto;
     max-width: 600px;
     padding: 20px;
 }
 label{
     display: block;
     margin:20px 0 10px;
 }
 input[type="text"],textarea,select{
     display: block;
     width: 100%;
     padding: 8px;
 }
 textarea{
     height: 200px;
 }
 #checkboxes label{
      display: inline-block;
      margin-top: 0px;
 }
#checkboxes input{
    display: inline-block;
    margin-right: 10px;
}
button{
    display: block;
    margin:20px 0;
    background: crimson;
    color: #fff;
    border: 0;
    padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;
}
#preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
}
h3{
   margin-top: 10px; 
}
</style>